/*///// BACK TO TOP */

#back-to-top {
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 9999;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 52px;
    background: transparent;
    cursor: pointer;
    color:#1FAFB1;
    border-radius: 2px;
    text-decoration: none;
    transition: opacity 0.4s ease-out;
    transition: opacity 0.4s ease-out;
    opacity: 0;
    border: 1px solid #1FAFB1;
    border-radius: 50px;
    font-size: 22px;
}
#back-to-top:hover {
    background: #1FAFB1;
    color: white;
    transition: 0.9s;
    transform: rotateY(180deg);
}
#back-to-top.show {
    opacity: 1;
}
